import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { clubApi } from '../services/clubApi';
import EventForm from '../components/EventForm';
import EventParticipants from '../components/EventParticipants';
import EventDetailsModal from '../components/EventDetailsModal';
import { getEventStatus, getEventStatusText, getTimeUntilStart, getTimeUntilEnd } from '../utils/eventStatus';
import '../styles/ClubEvents.css';

// 
const PlusIcon = () => (
  <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
    <line x1="12" y1="5" x2="12" y2="19"/>
    <line x1="5" y1="12" x2="19" y2="12"/>
  </svg>
);

const CalendarIcon = () => (
  <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
    <rect x="3" y="4" width="18" height="18" rx="2" ry="2"/>
    <line x1="16" y1="2" x2="16" y2="6"/>
    <line x1="8" y1="2" x2="8" y2="6"/>
    <line x1="3" y1="10" x2="21" y2="10"/>
  </svg>
);

const MapPinIcon = () => (
  <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
    <path d="M21 10c0 7-9 13-9 13s-9-6-9-13a9 9 0 0 1 18 0z"/>
    <circle cx="12" cy="10" r="3"/>
  </svg>
);

const UsersIcon = () => (
  <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
    <path d="M17 21v-2a4 4 0 0 0-4-4H5a4 4 0 0 0-4 4v2"/>
    <circle cx="9" cy="7" r="4"/>
    <path d="M23 21v-2a4 4 0 0 0-3-3.87"/>
    <path d="M16 3.13a4 4 0 0 1 0 7.75"/>
  </svg>
);

const ClockIcon = () => (
  <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
    <circle cx="12" cy="12" r="10"/>
    <polyline points="12,6 12,12 16,14"/>
  </svg>
);

const PencilIcon = () => (
  <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
    <path d="M11 4H4a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2v-7"/>
    <path d="M18.5 2.5a2.121 2.121 0 0 1 3 3L12 15l-4 1 1-4 9.5-9.5z"/>
  </svg>
);

const TrashIcon = () => (
  <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
    <polyline points="3,6 5,6 21,6"/>
    <path d="M19,6v14a2,2 0 0,1 -2,2H7a2,2 0 0,1 -2,-2V6m3,0V4a2,2 0 0,1 2,-2h4a2,2 0 0,1 2,2v2"/>
  </svg>
);

const EyeIcon = () => (
  <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
    <path d="M1 12s4-8 11-8 11 8 11 8-4 8-11 8-11-8-11-8z"/>
    <circle cx="12" cy="12" r="3"/>
  </svg>
);

const FunnelIcon = () => (
  <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
    <polygon points="22,3 2,3 10,12.46 10,19 14,21 14,12.46 22,3"/>
  </svg>
);

const MagnifyingGlassIcon = () => (
  <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
    <circle cx="11" cy="11" r="8"/>
    <path d="M21 21l-4.35-4.35"/>
  </svg>
);

const ClubEvents = () => {
  const [events, setEvents] = useState([]);
  const [loading, setLoading] = useState(true);
  const [filter, setFilter] = useState('all');
  const [searchTerm, setSearchTerm] = useState('');
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [editingEvent, setEditingEvent] = useState(null);
  const [clubId, setClubId] = useState(null);
  const [showParticipants, setShowParticipants] = useState(false);
  const [selectedEventId, setSelectedEventId] = useState(null);
  const [showEventDetails, setShowEventDetails] = useState(false);

  useEffect(() => {
    loadEvents();
  }, []);

  const loadEvents = async () => {
    try {
      const eventsData = await clubApi.getEvents();
      setEvents(Array.isArray(eventsData.events) ? eventsData.events : (Array.isArray(eventsData) ? eventsData : []));
    } catch (error) {
      console.error('  :', error);
      setEvents([]);
    } finally {
      setLoading(false);
    }
  };

  const handleDeleteEvent = async (eventId) => {
    if (window.confirm(' ,     ?')) {
      try {
        await clubApi.deleteEvent(eventId);
        setEvents((events || []).filter(event => event.id !== eventId));
      } catch (error) {
        console.error('  :', error);
      }
    }
  };

  const handleEditEvent = (event) => {
    setEditingEvent(event);
    setShowCreateModal(true);
  };

  const handleCreateEvent = () => {
    setEditingEvent(null);
    setShowCreateModal(true);
  };

  const handleCloseModal = () => {
    setShowCreateModal(false);
    setEditingEvent(null);
  };

  const handleEventSaved = () => {
    setShowCreateModal(false);
    setEditingEvent(null);
    loadEvents(); //   
  };

  const handleShowParticipants = (eventId) => {
    setSelectedEventId(eventId);
    setShowParticipants(true);
  };

  const handleCloseParticipants = () => {
    setShowParticipants(false);
    setSelectedEventId(null);
  };

  const handleShowEventDetails = (eventId) => {
    console.log('Opening event details for ID:', eventId);
    setSelectedEventId(eventId);
    setShowEventDetails(true);
  };

  const handleCloseEventDetails = () => {
    setShowEventDetails(false);
    setSelectedEventId(null);
  };

  const getEventStatusLocal = (event) => {
    //      
    return getEventStatus(
      event.date, 
      event.duration_hours || 2, 
      event.end_date
    );
  };

  const filteredEvents = (events || []).filter(event => {
    const eventStatus = getEventStatusLocal(event);
    const matchesFilter = filter === 'all' || filter === eventStatus;
    
    const matchesSearch = event.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         event.location.toLowerCase().includes(searchTerm.toLowerCase());
    
    return matchesFilter && matchesSearch;
  });

  if (loading) {
    return (
      <div className="club-events-loading">
        <div className="loading-spinner"></div>
        <p> ...</p>
      </div>
    );
  }

  return (
    <div className="club-events">
      {/* Header */}
      <div className="events-header">
        <div className="header-content">
          <h1> </h1>
          <p>     </p>
        </div>
        <button 
          className="btn btn-primary create-event-btn"
          onClick={handleCreateEvent}
        >
          <PlusIcon className="icon" />
           
        </button>
      </div>

      {/* Filters and Search */}
      <div className="events-controls">
        <div className="search-box">
          <MagnifyingGlassIcon className="search-icon" />
          <input
            type="text"
            placeholder=" ..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="search-input"
          />
        </div>
        
        <div className="filter-buttons">
          <button
            className={`filter-btn ${filter === 'all' ? 'active' : ''}`}
            onClick={() => setFilter('all')}
          >
            
          </button>
          <button
            className={`filter-btn ${filter === 'upcoming' ? 'active' : ''}`}
            onClick={() => setFilter('upcoming')}
          >
            
          </button>
          <button
            className={`filter-btn ${filter === 'ongoing' ? 'active' : ''}`}
            onClick={() => setFilter('ongoing')}
          >
             
          </button>
          <button
            className={`filter-btn ${filter === 'completed' ? 'active' : ''}`}
            onClick={() => setFilter('completed')}
          >
            
          </button>
        </div>
      </div>

      {/* Events Grid */}
      <div className="events-container">
        {filteredEvents.length === 0 ? (
          <div className="no-events">
            <CalendarIcon className="no-events-icon" />
            <h3>  </h3>
            <p>     </p>
            <button 
              className="btn btn-primary"
              onClick={handleCreateEvent}
            >
              <PlusIcon className="icon" />
               
            </button>
          </div>
        ) : (
          <div className="events-grid">
            {filteredEvents.map(event => (
              <div key={event.id} className="event-card">
                <div className="event-header">
                  <div className="event-status-badge">
                    <span className={`status-dot ${getEventStatusLocal(event)}`}></span>
                    {getEventStatusText(getEventStatusLocal(event))}
                  </div>
                  <div className="event-actions">
                    <button 
                      className="action-btn view"
                      onClick={(e) => {
                        e.preventDefault();
                        e.stopPropagation();
                        handleShowEventDetails(event.id);
                      }}
                      title=" "
                    >
                      <EyeIcon className="icon" />
                    </button>
                    <button 
                      className="action-btn participants"
                      onClick={() => handleShowParticipants(event.id)}
                      title=" "
                    >
                      <UsersIcon className="icon" />
                    </button>
                    <button 
                      className="action-btn edit"
                      onClick={() => handleEditEvent(event)}
                    >
                      <PencilIcon className="icon" />
                    </button>
                    <button 
                      className="action-btn delete"
                      onClick={() => handleDeleteEvent(event.id)}
                    >
                      <TrashIcon className="icon" />
                    </button>
                  </div>
                </div>
                
                <div className="event-content">
                  <h3 className="event-title">{event.title}</h3>
                  <p className="event-description">{event.description}</p>
                  
                  <div className="event-details">
                    <div className="detail-item">
                      <CalendarIcon className="icon" />
                      <span>{new Date(event.date).toLocaleDateString('ru-RU')}</span>
                      {event.time && <span className="event-time">  {event.time}</span>}
                    </div>
                    
                    {/*    / */}
                    {(() => {
                      const status = getEventStatusLocal(event);
                      const timeUntilStart = getTimeUntilStart(event.date);
                      const timeUntilEnd = getTimeUntilEnd(event.date, event.duration_hours || 2, event.end_date);
                      
                      if (status === 'upcoming' && timeUntilStart) {
                        return (
                          <div className="detail-item time-info">
                            <ClockIcon className="icon" />
                            <span> : {timeUntilStart}</span>
                          </div>
                        );
                      } else if (status === 'ongoing' && timeUntilEnd) {
                        return (
                          <div className="detail-item time-info">
                            <ClockIcon className="icon" />
                            <span> : {timeUntilEnd}</span>
                          </div>
                        );
                      }
                      return null;
                    })()}
                    
                    <div className="detail-item">
                      <MapPinIcon className="icon" />
                      <span>{event.location}</span>
                    </div>
                    
                    <div className="detail-item">
                      <UsersIcon className="icon" />
                      <span>
                        {event.max_participants && event.max_participants > 0 
                          ? `${event.current_participants || 0}/${event.max_participants} `
                          : `${event.current_participants || 0} `
                        }
                      </span>
                    </div>
                  </div>
                  
                  {event.price > 0 && (
                    <div className="event-price">
                      <span className="price-label">:</span>
                      <span className="price-value">{event.price} </span>
                    </div>
                  )}
                  
                  {event.is_premium && (
                    <div className="premium-badge">
                      <span></span>
                    </div>
                  )}
                </div>
                
                <div className="event-footer">
                  {event.max_participants ? (
                    <div className="participation-progress">
                      <div className="progress-bar">
                        <div 
                          className="progress-fill"
                          style={{ width: `${Math.min((event.current_participants || 0) / event.max_participants * 100, 100)}%` }}
                        ></div>
                      </div>
                      <span className="progress-text">
                        {Math.round((event.current_participants || 0) / event.max_participants * 100)}% 
                      </span>
                    </div>
                  ) : (
                    <div className="participation-progress">
                      <span className="progress-text">
                         
                      </span>
                    </div>
                  )}
                  
                  <Link to={`/club/events/${event.id}/participants`} className="btn btn-sm btn-secondary">
                    
                  </Link>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>

      {/* Event Form Modal */}
      {showCreateModal && (
        <EventForm
          event={editingEvent}
          onSave={handleEventSaved}
          onCancel={handleCloseModal}
          clubId={clubId}
        />
      )}

      {/* Event Participants Modal */}
      {showParticipants && (
        <EventParticipants
          eventId={selectedEventId}
          onClose={handleCloseParticipants}
        />
      )}

      {/* Event Details Modal */}
      <EventDetailsModal
        isOpen={showEventDetails}
        onClose={handleCloseEventDetails}
        eventId={selectedEventId}
      />
    </div>
  );
};

export default ClubEvents;
