import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import { useMutation } from 'react-query';
import toast from 'react-hot-toast';
import { clubApi } from '../services/clubApi';
import { authAPI } from '../services/api';
import {
  PageContainer,
  ContentCard,
  LogoSection,
  Logo,
  Title,
  Subtitle,
  Form,
  FormGroup,
  Label,
  InputWrapper,
  Input,
  TextArea,
  Button,
  ErrorText,
  FlexContainer,
  EyeIcon,
  EyeOffIcon,
  IconButton
} from '../components/UI';
import '../styles/AuthPages.css';

const ClubRegister = () => {
  const navigate = useNavigate();
  const [isLoading, setIsLoading] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const [emailCode, setEmailCode] = useState('');
  const [codeTimer, setCodeTimer] = useState(0);
  const [isCodeSent, setIsCodeSent] = useState(false);
  
  const {
    register,
    handleSubmit,
    watch,
    formState: { errors }
  } = useForm();

  const password = watch('password');
  const email = watch('email');

  //    
  const sendCodeMutation = useMutation(authAPI.sendCode, {
    onSuccess: () => {
      toast.success('     email');
      setIsCodeSent(true);
      setCodeTimer(60);
      const interval = setInterval(() => {
        setCodeTimer(prev => {
          if (prev <= 1) {
            clearInterval(interval);
            return 0;
          }
          return prev - 1;
        });
      }, 1000);
    },
    onError: (error) => {
      toast.error('  : ' + (error.message || ' '));
    }
  });

  //    
  const registerMutation = useMutation(clubApi.register, {
    onSuccess: (response) => {
      if (response.success || response.token) {
        toast.success('  !');
        navigate('/club/login');
      }
    },
    onError: (error) => {
      const errorMessage = error.message || ' ';
      toast.error(errorMessage);
    }
  });

  const handleSendCode = () => {
    if (!email) {
      toast.error('  email');
      return;
    }
    sendCodeMutation.mutate(email);
  };

  const onSubmit = async (data) => {
    if (!emailCode) {
      toast.error('    email');
      return;
    }

    registerMutation.mutate({
      name: data.name,
      login: data.login,
      email: data.email,
      password: data.password,
      description: data.description,
      location: data.location,
      contact_info: data.contact_info,
      website: data.website,
      type: 'general', //  -  ""
      mail_code: emailCode
    });
  };

  return (
    <PageContainer $gradient>
      {/*   */}
      <div className="auth-decoration auth-decoration-top-right"></div>
      <div className="auth-decoration auth-decoration-bottom-left"></div>
      
      <ContentCard $maxWidth="600px">
        <LogoSection>
          <Logo>CF</Logo>
          <Title> </Title>
          <Subtitle>     </Subtitle>
        </LogoSection>

        <Form onSubmit={handleSubmit(onSubmit)}>
          <FormGroup>
            <Label htmlFor="name">  *</Label>
            <Input
              type="text"
              id="name"
              placeholder="  "
              {...register('name', {
                required: '  ',
                minLength: {
                  value: 2,
                  message: ' 2 '
                },
                maxLength: {
                  value: 100,
                  message: ' 100 '
                }
              })}
              className={errors.name ? 'error' : ''}
            />
            {errors.name && <ErrorText>{errors.name.message}</ErrorText>}
          </FormGroup>

          <FormGroup>
            <Label htmlFor="login"> *</Label>
            <Input
              type="text"
              id="login"
              placeholder="  "
              {...register('login', {
                required: ' ',
                minLength: {
                  value: 3,
                  message: ' 3 '
                },
                maxLength: {
                  value: 50,
                  message: ' 50 '
                },
                pattern: {
                  value: /^[a-zA-Z0-9_-]+$/,
                  message: ' , ,   '
                }
              })}
              className={errors.login ? 'error' : ''}
            />
            {errors.login && <ErrorText>{errors.login.message}</ErrorText>}
          </FormGroup>

          <FormGroup>
            <Label htmlFor="email">Email *</Label>
            <div style={{ display: 'flex', gap: '10px' }}>
              <div style={{ flex: 1 }}>
                <Input
                  type="email"
                  id="email"
                  placeholder=" email "
                  {...register('email', {
                    required: 'Email ',
                    pattern: {
                      value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                      message: ' email'
                    }
                  })}
                  className={errors.email ? 'error' : ''}
                />
              </div>
              <button
                type="button"
                onClick={handleSendCode}
                disabled={!email || sendCodeMutation.isLoading || codeTimer > 0}
                style={{
                  background: codeTimer > 0 ? '#e2e8f0' : '#dc3522',
                  color: 'white',
                  border: 'none',
                  borderRadius: '8px',
                  padding: '12px 16px',
                  fontSize: '14px',
                  fontWeight: '600',
                  cursor: codeTimer > 0 ? 'not-allowed' : 'pointer',
                  transition: 'all 0.3s ease',
                  whiteSpace: 'nowrap'
                }}
              >
                {sendCodeMutation.isLoading ? '...' : 
                 codeTimer > 0 ? `${codeTimer}` : ''}
              </button>
            </div>
            {errors.email && <ErrorText>{errors.email.message}</ErrorText>}
            {codeTimer > 0 && (
              <div style={{ 
                fontSize: '12px', 
                color: '#718096', 
                marginTop: '5px' 
              }}>
                   {codeTimer} 
              </div>
            )}
          </FormGroup>

          <FormGroup>
            <Label htmlFor="emailCode">  email *</Label>
            <Input
              type="text"
              id="emailCode"
              placeholder=" 6- "
              value={emailCode}
              onChange={(e) => {
                //      6 
                const value = e.target.value.replace(/\D/g, '').slice(0, 6);
                setEmailCode(value);
              }}
              style={{
                fontFamily: 'monospace',
                fontSize: '18px',
                letterSpacing: '2px',
                textAlign: 'center'
              }}
            />
            {emailCode.length > 0 && emailCode.length < 6 && isCodeSent && (
              <ErrorText>   6 </ErrorText>
            )}
            {!isCodeSent && emailCode.length > 0 && (
              <ErrorText>    email</ErrorText>
            )}
          </FormGroup>



          <FormGroup>
            <Label htmlFor="location"> ()</Label>
            <Input
              type="text"
              id="location"
              placeholder=",    "
              {...register('location', {
                maxLength: {
                  value: 200,
                  message: ' 200 '
                }
              })}
              className={errors.location ? 'error' : ''}
            />
            {errors.location && <ErrorText>{errors.location.message}</ErrorText>}
          </FormGroup>

          <FormGroup>
            <Label htmlFor="website">- ()</Label>
            <Input
              type="text"
              id="website"
              placeholder="    "
              {...register('website')}
              className={errors.website ? 'error' : ''}
            />
            {errors.website && <ErrorText>{errors.website.message}</ErrorText>}
          </FormGroup>

          <FormGroup>
            <Label htmlFor="contact_info">  ()</Label>
            <TextArea
              id="contact_info"
              placeholder=",  ,     "
              $minHeight="80px"
              {...register('contact_info', {
                maxLength: {
                  value: 500,
                  message: ' 500 '
                }
              })}
              className={errors.contact_info ? 'error' : ''}
            />
            {errors.contact_info && <ErrorText>{errors.contact_info.message}</ErrorText>}
          </FormGroup>

          <FormGroup>
            <Label htmlFor="description">  *</Label>
            <TextArea
              id="description"
              placeholder="   , , "
              $minHeight="120px"
              {...register('description', {
                required: '  ',
                maxLength: {
                  value: 1000,
                  message: ' 1000 '
                }
              })}
              className={errors.description ? 'error' : ''}
            />
            {errors.description && <ErrorText>{errors.description.message}</ErrorText>}
          </FormGroup>

          <FormGroup>
            <Label htmlFor="password"> *</Label>
            <InputWrapper>
              <Input
                type={showPassword ? 'text' : 'password'}
                id="password"
                placeholder="  ( 8 )"
                {...register('password', {
                  required: ' ',
                  minLength: {
                    value: 8,
                    message: ' 8 '
                  }
                })}
                className={errors.password ? 'error' : ''}
              />
              <IconButton
                type="button"
                $variant="secondary"
                onClick={() => setShowPassword(!showPassword)}
                style={{
                  position: 'absolute',
                  right: '12px',
                  background: 'none',
                  color: '#718096'
                }}
              >
                {showPassword ? <EyeOffIcon /> : <EyeIcon />}
              </IconButton>
            </InputWrapper>
            {errors.password && <ErrorText>{errors.password.message}</ErrorText>}
            <div style={{ 
              fontSize: '12px', 
              color: '#718096', 
              marginTop: '5px' 
            }}>
              :      
            </div>
          </FormGroup>

          <FormGroup>
            <Label htmlFor="confirmPassword">  *</Label>
            <InputWrapper>
              <Input
                type={showConfirmPassword ? 'text' : 'password'}
                id="confirmPassword"
                placeholder=" "
                {...register('confirmPassword', {
                  required: ' ',
                  validate: value => value === password || '  '
                })}
                className={errors.confirmPassword ? 'error' : ''}
              />
              <IconButton
                type="button"
                $variant="secondary"
                onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                style={{
                  position: 'absolute',
                  right: '12px',
                  background: 'none',
                  color: '#718096'
                }}
              >
                {showConfirmPassword ? <EyeOffIcon /> : <EyeIcon />}
              </IconButton>
            </InputWrapper>
            {errors.confirmPassword && <ErrorText>{errors.confirmPassword.message}</ErrorText>}
          </FormGroup>

          <Button 
            type="submit" 
            disabled={registerMutation.isLoading || !emailCode || emailCode.length !== 6} 
            style={{ width: '100%' }}
          >
            {registerMutation.isLoading ? '...' : ' '}
          </Button>
        </Form>

        <FlexContainer $justify="center" $gap="20px" style={{ marginTop: '25px' }}>
          <Link 
            to="/club/login" 
            style={{ 
              color: '#dc3522', 
              textDecoration: 'none', 
              fontWeight: '600',
              fontSize: '14px'
            }}
          >
              ? 
          </Link>
        </FlexContainer>

        <FlexContainer $justify="center" style={{ marginTop: '20px' }}>
          <Link 
            to="/register" 
            style={{ 
              color: '#718096', 
              textDecoration: 'none', 
              fontWeight: '500',
              fontSize: '13px'
            }}
          >
              
          </Link>
        </FlexContainer>
      </ContentCard>
    </PageContainer>
  );
};

export default ClubRegister;
