const Home = () => {
  const [currentProfile, setCurrentProfile] = useState(null);
  const [showHint, setShowHint] = useState(false);
  const [showSuperlikeModal, setShowSuperlikeModal] = useState(false);
  const [superlikeMessage, setSuperlikeMessage] = useState('');
  const [swipeDirection, setSwipeDirection] = useState(null); // 'left'  , 'right'  
  const [profileQueue, setProfileQueue] = useState([]); //   
  const [isPreloading, setIsPreloading] = useState(false); //  
  
  //     
  const [profileHistory, setProfileHistory] = useState([]); //    ( 3)
  const [historyIndex, setHistoryIndex] = useState(-1); //    (-1 =  )
  
  //  :      
  const [profileShowKey, setProfileShowKey] = useState(0);
  
  //    
  const [isRestoring, setIsRestoring] = useState(true);
  
  const queryClient = useQueryClient();
  const currentUser = apiUtils.getCurrentUser();
  const { showMatchPopup } = useNotifications();

  //      
  const saveProfilesState = () => {
    try {
      const stateToSave = {
        currentProfile,
        profileQueue,
        profileHistory,
        historyIndex,
        profileShowKey,
        timestamp: Date.now()
      };
      sessionStorage.setItem('home-profiles-state', JSON.stringify(stateToSave));
      console.log('     sessionStorage');
    } catch (error) {
      console.error('  :', error);
    }
  };

  const restoreProfilesState = () => {
    try {
      const savedState = sessionStorage.getItem('home-profiles-state');
      if (savedState) {
        const parsedState = JSON.parse(savedState);
        const now = Date.now();
        const timeDiff = now - parsedState.timestamp;
        
        //        30 
        if (timeDiff < 30 * 60 * 1000) {
          setCurrentProfile(parsedState.currentProfile);
          setProfileQueue(parsedState.profileQueue || []);
          setProfileHistory(parsedState.profileHistory || []);
          setHistoryIndex(parsedState.historyIndex || -1);
          setProfileShowKey(parsedState.profileShowKey || 0);
          console.log('     sessionStorage');
          return true;
        } else {
          console.log('   , ');
          sessionStorage.removeItem('home-profiles-state');
        }
      }
    } catch (error) {
      console.error('  :', error);
      sessionStorage.removeItem('home-profiles-state');
    }
    
    //     false
    setIsRestoring(false);
    return false;
  };

  const clearProfilesState = () => {
    try {
      sessionStorage.removeItem('home-profiles-state');
      console.log('     sessionStorage');
    } catch (error) {
      console.error('  :', error);
    }
  };



  //      
  const addToHistory = (profile) => {
    if (!profile) return;
    
    setProfileHistory(prev => {
      const newHistory = [...prev, profile];
      //   3 
      if (newHistory.length > 3) {
        return newHistory.slice(-3);
      }
      return newHistory;
    });
    setHistoryIndex(prev => Math.min(prev + 1, 2)); //   2 (3 )
    
    //  
    console.log('  :', profile.login, ':', profileHistory.length + 1);
  };

  //   
  const preloadProfiles = async (count = 10) => {
    if (isPreloading) return;
    
    setIsPreloading(true);
    
    try {
      //     ,  
      
      console.log(' ===   ===');
      console.log('  :', { count, currentQueueLength: profileQueue.length });
      
      //    batch 
      const newProfiles = await swipeAPI.getProfilesBatch(count, []);
      
      console.log(' ===     ===');
      console.log('    :', newProfiles?.length || 0);
      
      if (newProfiles && newProfiles.length > 0) {
        //    
        newProfiles.forEach((profile, index) => {
          console.log(`  ${index + 1}:`, {
            id: profile.id,
            login: profile.login,
            ava: profile.ava,
            status: profile.status,
            city: profile.city,
            distance: profile.distance,
            age: profile.age,
            viptype: profile.viptype,
            isCouple: profile.isCouple,
            height: profile.height,
            weight: profile.weight,
            smoking: profile.smoking,
            alko: profile.alko
          });
        });
        
        if (profileQueue.length === 0 && !currentProfile) {
          //    ,   
          console.log('     :', newProfiles[0].login);
          setCurrentProfile(newProfiles[0]);
          setProfileQueue(newProfiles.slice(1));
          console.log('     :', newProfiles.slice(1).map(p => p.login));
        } else {
          //    
          console.log('     ');
          const oldQueueLength = profileQueue.length;
          setProfileQueue(prev => [...prev, ...newProfiles]);
          console.log(`  : ${oldQueueLength}  ${oldQueueLength + newProfiles.length}`);
        }
      } else {
        console.warn('      null');
      }
      
      console.log(' ===   ===');
      
    } catch (error) {
      //   ,    
      console.error('   :', error);
    } finally {
      setIsPreloading(false);
    }
  };

  //    
  const getNextProfile = () => {
    console.log(' ===    ===');
    console.log('  :', {
      currentProfile: currentProfile?.login || 'null',
      profileQueueLength: profileQueue.length,
      historyLength: profileHistory.length,
      historyIndex
    });
    
    if (profileQueue.length > 0) {
      //     
      if (currentProfile) {
        console.log('   :', currentProfile.login);
        addToHistory(currentProfile);
      }
      
      //    
      const nextProfile = profileQueue[0];
      console.log('    :', nextProfile.login);
      
      //   
      setProfileQueue(prev => prev.slice(1));
      setCurrentProfile(nextProfile);
      
      //  :    
      setProfileShowKey(prev => prev + 1);
      
      console.log('  :', {
        newCurrentProfile: nextProfile.login,
        newQueueLength: profileQueue.length - 1,
        newProfileShowKey: profileShowKey + 1
      });
      
      //     3 ,   10
      if (profileQueue.length <= 3) {
        console.log('   ( <= 3)');
        preloadProfiles(10);
      }
      
      console.log(' ===    ===');
      return nextProfile;
    } else {
      //   ,   
      console.log('  ,  ');
      preloadProfiles(10);
      
      // :   ,    
      //        (   )
      console.log('      :', currentProfile?.login);
      console.log(' ===   () ===');
      return currentProfile;
    }
  };

  //   -   batch 
  const { data: initialProfiles, isLoading, refetch } = useQuery(
    'initial-profiles',
    () => swipeAPI.getProfilesBatch(10, []), //   
    {
      enabled: !currentProfile && profileQueue.length === 0, //      
      onSuccess: (data) => {
        if (data && data.length > 0) {
          //    
          setCurrentProfile(data[0]);
          //    
          setProfileQueue(data.slice(1));
          //  :   
          setProfileShowKey(1);
          //    
          preloadProfiles(10);
        } else {
          setCurrentProfile(null);
        }
        setSwipeDirection(null);
      },
      onError: (error) => {
        console.error('Error loading initial profiles:', error);
        if (error.response?.data?.error === 'no_profiles') {
          setCurrentProfile(null);
        } else {
          toast.error(apiUtils.handleError(error));
        }
        setSwipeDirection(null);
      }
    }
  );

  //   
  const likeMutation = useMutation(
    ({ targetUser, source }) => swipeAPI.like(targetUser, source),
    {
      onSuccess: async (data) => {
        if (data.result === 'reciprocal_like' || data.match_created) {
          // ,        
          if (currentProfile) {
            try {
              const matchStatus = await swipeAPI.checkExistingMatch(currentProfile.login);
              
              //        
              if (!matchStatus.hasMatch) {
                showMatchPopup({
                  username: currentProfile.login,
                  userData: {
                    avatar: currentProfile.ava,
                    login: currentProfile.login
                  }
                });
                toast.success(' ! ', { duration: 6000 });
              } else {
                toast.success(' ! ');
              }
            } catch (error) {
              console.error('Error checking existing match:', error);
              //       
              showMatchPopup({
                username: currentProfile.login,
                userData: {
                  avatar: currentProfile.ava,
                  login: currentProfile.login
                }
              });
              toast.success(' ! ', { duration: 6000 });
            }
          }
        } else {
          toast.success(' ! ');
        }
        //    
        setSwipeDirection(null);
        getNextProfile(); //     
        
        if (!currentProfile) {
          //    ,   
          setCurrentProfile(null);
          //     
        }
      },
      onError: (error) => {
        toast.error(apiUtils.handleError(error));
        setSwipeDirection(null);
      }
    }
  );

  const dislikeMutation = useMutation(
    ({ targetUser, source }) => swipeAPI.dislike(targetUser, source),
    {
      onSuccess: () => {
        //    
        setSwipeDirection(null);
        getNextProfile(); //     
      },
      onError: (error) => {
        toast.error(apiUtils.handleError(error));
        setSwipeDirection(null);
      }
    }
  );

  const superlikeMutation = useMutation(
    ({ targetUser, message }) => swipeAPI.superlike({ targetUser, message }),
    {
      onSuccess: async (data) => {
        setShowSuperlikeModal(false);
        setSuperlikeMessage('');
        
        //     -    
        if (currentProfile && (data.result === 'reciprocal_like' || data.match_created)) {
          try {
            const matchStatus = await swipeAPI.checkExistingMatch(currentProfile.login);
            
            //        
            if (!matchStatus.hasMatch) {
              showMatchPopup({
                username: currentProfile.login,
                userData: {
                  avatar: currentProfile.ava,
                  login: currentProfile.login
                }
              });
              toast.success(' ! ', { duration: 6000 });
            } else {
              toast.success(' ! ');
            }
          } catch (error) {
            console.error('Error checking existing match:', error);
            //       
            showMatchPopup({
              username: currentProfile.login,
              userData: {
                avatar: currentProfile.ava,
                login: currentProfile.login
              }
            });
            toast.success(' ! ', { duration: 6000 });
          }
        } else {
          toast.success(' ! ');
        }
        
        //    
        setSwipeDirection(null);
        getNextProfile(); //     
      },
      onError: (error) => {
        toast.error(apiUtils.handleError(error));
      }
    }
  );

  //     
  useEffect(() => {
    const hasSeenHint = localStorage.getItem('swipe-hint-seen');
    if (!hasSeenHint && currentProfile) {
      setShowHint(true);
      setTimeout(() => {
        setShowHint(false);
        localStorage.setItem('swipe-hint-seen', 'true');
      }, 3000);
    }
  }, [currentProfile]);

  //    
  useEffect(() => {
    if (currentProfile && profileQueue.length === 0) {
      preloadProfiles(5);
    }
  }, [currentProfile]);

  //     
  useEffect(() => {
    if (currentProfile || profileQueue.length > 0 || profileHistory.length > 0) {
      saveProfilesState();
    }
  }, [currentProfile, profileQueue, profileHistory, historyIndex, profileShowKey]);

  //     
  useEffect(() => {
    const stateRestored = restoreProfilesState();
    
    //    ,   
    if (!stateRestored) {
      console.log('   ,   ');
      setIsRestoring(false);
    } else {
      console.log('  ,   ');
      //     ,  
      if (profileQueue.length <= 3) {
        preloadProfiles(10);
      }
      setIsRestoring(false);
    }

    //    
    return () => {
      //    
      saveProfilesState();
    };
  }, []);

  //     
  useEffect(() => {
    const handleAuthLogout = () => {
      console.log('  ,   ');
      clearProfilesState();
    };

    const handleAuthLogin = () => {
      console.log('  ,   ');
      clearProfilesState();
    };

    const handleProfileUpdate = () => {
      console.log('  ,   ');
      clearProfilesState();
    };

    const handleLocationChange = () => {
      console.log('  ,   ');
      clearProfilesState();
    };

    const handleFiltersChange = () => {
      console.log('  ,   ');
      clearProfilesState();
    };

    const handleSubscriptionChange = () => {
      console.log('  ,   ');
      clearProfilesState();
    };

    const handlePrivacyChange = () => {
      console.log('   ,   ');
      clearProfilesState();
    };

    const handleNotificationsChange = () => {
      console.log('   ,   ');
      clearProfilesState();
    };

    const handleLanguageChange = () => {
      console.log('  ,   ');
      clearProfilesState();
    };

    const handleThemeChange = () => {
      console.log('  ,   ');
      clearProfilesState();
    };

    const handleMessageNotificationsChange = () => {
      console.log('     ,   ');
      clearProfilesState();
    };

    const handleLikeNotificationsChange = () => {
      console.log('     ,   ');
      clearProfilesState();
    };

    const handleMatchNotificationsChange = () => {
      console.log('     ,   ');
      clearProfilesState();
    };

    const handleVisitNotificationsChange = () => {
      console.log('     ,   ');
      clearProfilesState();
    };

    const handleCommentNotificationsChange = () => {
      console.log('     ,   ');
      clearProfilesState();
    };

    const handleGiftNotificationsChange = () => {
      console.log('     ,   ');
      clearProfilesState();
    };

    const handleEventNotificationsChange = () => {
      console.log('     ,   ');
      clearProfilesState();
    };

    const handleClubNotificationsChange = () => {
      console.log('     ,   ');
      clearProfilesState();
    };

    const handleRatingNotificationsChange = () => {
      console.log('     ,   ');
      clearProfilesState();
    };

    const handleReportNotificationsChange = () => {
      console.log('     ,   ');
      clearProfilesState();
    };

    const handleModerationNotificationsChange = () => {
      console.log('     ,   ');
      clearProfilesState();
    };

    const handleSystemNotificationsChange = () => {
      console.log('      ,   ');
      clearProfilesState();
    };

    window.addEventListener('auth-logout', handleAuthLogout);
    window.addEventListener('auth-login', handleAuthLogin);
    window.addEventListener('profile-updated', handleProfileUpdate);
    window.addEventListener('location-changed', handleLocationChange);
    window.addEventListener('filters-changed', handleFiltersChange);
    window.addEventListener('subscription-changed', handleSubscriptionChange);
    window.addEventListener('privacy-changed', handlePrivacyChange);
    window.addEventListener('notifications-changed', handleNotificationsChange);
    window.addEventListener('language-changed', handleLanguageChange);
    window.addEventListener('theme-changed', handleThemeChange);
    window.addEventListener('message-notifications-changed', handleMessageNotificationsChange);
    window.addEventListener('like-notifications-changed', handleLikeNotificationsChange);
    window.addEventListener('match-notifications-changed', handleMatchNotificationsChange);
    window.addEventListener('visit-notifications-changed', handleVisitNotificationsChange);
    window.addEventListener('comment-notifications-changed', handleCommentNotificationsChange);
    window.addEventListener('gift-notifications-changed', handleGiftNotificationsChange);
    window.addEventListener('event-notifications-changed', handleEventNotificationsChange);
    window.addEventListener('club-notifications-changed', handleClubNotificationsChange);
    window.addEventListener('rating-notifications-changed', handleRatingNotificationsChange);
    window.addEventListener('report-notifications-changed', handleReportNotificationsChange);
    window.addEventListener('moderation-notifications-changed', handleModerationNotificationsChange);
    window.addEventListener('system-notifications-changed', handleSystemNotificationsChange);
    
    return () => {
      window.removeEventListener('auth-logout', handleAuthLogout);
      window.removeEventListener('auth-login', handleAuthLogin);
      window.removeEventListener('profile-updated', handleProfileUpdate);
      window.removeEventListener('location-changed', handleLocationChange);
      window.removeEventListener('filters-changed', handleFiltersChange);
      window.removeEventListener('subscription-changed', handleSubscriptionChange);
      window.removeEventListener('privacy-changed', handlePrivacyChange);
      window.removeEventListener('notifications-changed', handleNotificationsChange);
      window.removeEventListener('language-changed', handleLanguageChange);
      window.removeEventListener('theme-changed', handleThemeChange);
      window.removeEventListener('message-notifications-changed', handleMessageNotificationsChange);
      window.removeEventListener('like-notifications-changed', handleLikeNotificationsChange);
      window.removeEventListener('match-notifications-changed', handleMatchNotificationsChange);
      window.removeEventListener('visit-notifications-changed', handleVisitNotificationsChange);
      window.removeEventListener('comment-notifications-changed', handleCommentNotificationsChange);
      window.removeEventListener('gift-notifications-changed', handleGiftNotificationsChange);
      window.removeEventListener('event-notifications-changed', handleEventNotificationsChange);
      window.removeEventListener('club-notifications-changed', handleClubNotificationsChange);
      window.removeEventListener('rating-notifications-changed', handleRatingNotificationsChange);
      window.removeEventListener('report-notifications-changed', handleReportNotificationsChange);
      window.removeEventListener('moderation-notifications-changed', handleModerationNotificationsChange);
      window.removeEventListener('system-notifications-changed', handleSystemNotificationsChange);
    };
  }, []);

  //    
  const handleRefreshProfiles = () => {
    console.log('   ');
    clearProfilesState(); //   
    setCurrentProfile(null);
    setProfileQueue([]);
    setProfileHistory([]);
    setHistoryIndex(-1);
    setProfileShowKey(0);
    setIsRestoring(false);
    refetch(); //   
  };

  //  
  const handleLike = () => {
    if (currentProfile) {
      setSwipeDirection('right');
      //   
      likeMutation.mutate({
        targetUser: currentProfile.login,
        source: 'button'
      });
    }
  };

  const handleDislike = () => {
    if (currentProfile) {
      setSwipeDirection('left');
      //   
      dislikeMutation.mutate({
        targetUser: currentProfile.login,
        source: 'button'
      });
    }
  };

  const handleSuperlike = () => {
    if (currentProfile) {
      setShowSuperlikeModal(true);
    }
  };

  const handleSendSuperlike = () => {
    if (currentProfile && superlikeMessage.trim()) {
      superlikeMutation.mutate({
        targetUser: currentProfile.login,
        message: superlikeMessage.trim()
      });
    }
  };

  const handleCloseSuperlikeModal = () => {
    setShowSuperlikeModal(false);
    setSuperlikeMessage('');
  };

  const handleBack = () => {
    if (historyIndex >= 0 && profileHistory.length > 0) {
      //      
      if (currentProfile) {
        setProfileQueue(prev => [currentProfile, ...prev]);
      }
      
      //     
      const previousProfile = profileHistory[historyIndex];
      setCurrentProfile(previousProfile);
      
      //   
      setHistoryIndex(prev => prev - 1);
      
      //     (    )
      setProfileHistory(prev => prev.slice(0, historyIndex));
      
      //  
      console.log('  :', previousProfile.login, ' :', historyIndex - 1);
    } else {
      toast.error('  ');
      console.log('  ,   . :', historyIndex, ':', profileHistory.length);
    }
  };

  //  
  const handleDragEnd = (event, info) => {
    const threshold = 100;
    
    if (info.offset.x > threshold) {
      //   - 
      if (currentProfile) {
        setSwipeDirection('right');
        //   
        likeMutation.mutate({
          targetUser: currentProfile.login,
          source: 'gesture'
        });
      }
    } else if (info.offset.x < -threshold) {
      //   - 
      if (currentProfile) {
        setSwipeDirection('left');
        //   
        dislikeMutation.mutate({
          targetUser: currentProfile.login,
          source: 'gesture'
        });
      }
    }
  };

  if (isLoading || isRestoring) {
    return (
      <HomeContainer>
        <Header>
          <HeaderContent>
            <WelcomeText>
              <h1>SwingFox</h1>
              <p>{isRestoring ? ' ...' : ' ...'}</p>
            </WelcomeText>
          </HeaderContent>
        </Header>
        <LoadingSpinner />
      </HomeContainer>
    );
  }

  return (
    <HomeContainer>
      <Header>
        <HeaderContent>
          <WelcomeText>
            <h1>  </h1>
            <p>     </p>
          </WelcomeText>
          <UserInfo>
            <div className="user-details">
              <p className="username">@{currentUser?.login}</p>
              <p className="status"></p>
            </div>
            <Avatar
              $src={currentUser?.ava ? `/uploads/${currentUser.ava}` : ''}
              $size="50px"
              $fontSize="20px"
            >
              {!currentUser?.ava && currentUser?.login?.charAt(0).toUpperCase()}
            </Avatar>
          </UserInfo>
        </HeaderContent>
      </Header>

      <SwipeContainer>
        <SwipeHint $show={showHint}>
           /    
        </SwipeHint>
        
        <AnimatePresence mode="wait">
          {currentProfile ? (
            <ProfileCard
              //  :   login + profileShowKey
              key={`${currentProfile.login}-${profileShowKey}`}
              drag="x"
              dragConstraints={{ left: 0, right: 0 }}
              onDragEnd={handleDragEnd}
              initial={{ scale: 0, opacity: 0, rotateY: 90 }}
              animate={{ 
                scale: 1, 
                opacity: 1, 
                rotateY: 0,
                x: swipeDirection === 'left' ? -400 : swipeDirection === 'right' ? 400 : 0,
                rotate: swipeDirection === 'left' ? -20 : swipeDirection === 'right' ? 20 : 0
              }}
              exit={{ 
                scale: 0, 
                opacity: 0, 
                rotateY: swipeDirection === 'left' ? -90 : 90,
                x: swipeDirection === 'left' ? -400 : 400,
                rotate: swipeDirection === 'left' ? -20 : 20
              }}
              transition={{ 
                duration: swipeDirection ? 0.1 : 0.2,
                type: swipeDirection ? "tween" : "spring",
                stiffness: swipeDirection ? undefined : 200
              }}
              whileDrag={{ scale: 1.02, rotate: 2 }}
              onLoad={() => {
                console.log('===   ===');
                console.log(' :', {
                  login: currentProfile.login,
                  date: currentProfile.date,
                  age: currentProfile.age,
                  distance: currentProfile.distance,
                  city: currentProfile.city,
                  height: currentProfile.height,
                  weight: currentProfile.weight,
                  smoking: currentProfile.smoking,
                  alko: currentProfile.alko,
                  isCouple: currentProfile.isCouple,
                  partnerData: currentProfile.partnerData
                });
                
                //   
                console.log(' :', {
                  searchStatus: currentProfile.searchStatus,
                  searchAge: currentProfile.searchAge,
                  location: currentProfile.location
                });
                
                //    
                if (currentProfile.date && currentProfile.date.includes('_')) {
                  const ages = getAgeFromDate(currentProfile.date);
                  console.log('  date:', ages);
                }
                
                console.log('===   ===');
              }}
            >
              <ProfileImage $src={currentProfile.ava ? `/uploads/${currentProfile.ava}` : ''}>
                {!currentProfile.ava && ''}
                <ProfileOverlay>
                  <ClickableUsername to={`/profile/${currentProfile.login}`}>
                    @{currentProfile.login}
                  </ClickableUsername>
                  <div className="location">
                    <LocationIcon />
                    {currentProfile.city}, {currentProfile.distance}
                  </div>
                  <div className="age">
                    {currentProfile.age || '  '}
                  </div>
                </ProfileOverlay>
              </ProfileImage>
              
              <ProfileDetails>
                <div>
                  <div className="status-badge">
                    <InfoIcon />
                    {currentProfile.status}
                  </div>
                  
                  {/*   */}
                  {currentProfile.compatibility && (
                    <div className="compatibility-badge">
                      <span style={{ fontSize: '14px' }}></span>
                      <span>:</span>
                      <span className="percentage">
                        {Math.round(currentProfile.compatibility.totalScore * 100)}%
                      </span>
                      <div className="progress-bar">
                        <div 
                          className="progress-fill" 
                          style={{ 
                            width: `${Math.round(currentProfile.compatibility.totalScore * 100)}%` 
                          }}
                        />
                      </div>
                    </div>
                  )}
                  
                  {/*   ,    */}
                  {currentProfile.searchStatus && (
                    <div className="partner-info search-info">
                      <div className="title">  </div>
                      <div className="details">
                        <div>
                          <strong>:</strong> {currentProfile.searchStatus}
                        </div>
                      </div>
                    </div>
                  )}
                  
                  {/*    */}
                  {currentProfile.location && (
                    <div className="partner-info location-info">
                      <div className="title">  </div>
                      <div className="details">
                        <div>
                          <strong>:</strong>
                          <div style={{ marginTop: '4px', marginLeft: '8px' }}>
                            {currentProfile.location.split('&&').map((place, index) => (
                              <div key={index} style={{ marginBottom: '2px' }}>
                                 {place.trim()}
                              </div>
                            ))}
                          </div>
                        </div>
                      </div>
                    </div>
                  )}
                  
                  
                  {/*   -     */}
                  
                  {/*   */}
                  <div className="info">
                    {currentProfile.info ? 
                      (currentProfile.info.length > 120 ? currentProfile.info.substring(0, 120) + '...' : currentProfile.info) 
                      : '  '
                    }
                  </div>
                </div>
              </ProfileDetails>
            </ProfileCard>
          ) : (
            <NoMoreProfiles>
              <div className="icon"></div>
              <h3> !</h3>
              <p>       </p>
              <Button onClick={handleRefreshProfiles}>
                
              </Button>
            </NoMoreProfiles>
          )}
        </AnimatePresence>
      </SwipeContainer>

      {currentProfile && (
        <ActionButtons>
          <ActionButton 
            className="back" 
            onClick={handleBack} 
            title="   "
            disabled={historyIndex < 0} // ,   
          >
            <BackIcon />
          </ActionButton>
          
          <ActionButton 
            className="dislike" 
            onClick={handleDislike} 
            title=" "
            disabled={dislikeMutation.isLoading}
          >
            <DislikeIcon />
          </ActionButton>
          
          <ActionButton 
            className="superlike" 
            onClick={handleSuperlike} 
            title="  "
            disabled={superlikeMutation.isLoading}
          >
            <SuperlikeIcon />
          </ActionButton>
          
          <ActionButton 
            className="like" 
            onClick={handleLike} 
            title=""
            disabled={likeMutation.isLoading}
          >
            <HeartIcon />
          </ActionButton>
        </ActionButtons>
      )}

      {/*     */}
      {showSuperlikeModal && (
        <Modal onClick={handleCloseSuperlikeModal}>
          <ModalContent $maxWidth="500px" onClick={(e) => e.stopPropagation()}>
            <ModalHeader>
              <h2> </h2>
              <IconButton
                $variant="secondary"
                onClick={handleCloseSuperlikeModal}
              >
                <CloseIcon />
              </IconButton>
            </ModalHeader>
            
            <div style={{ padding: '20px' }}>
              <p style={{
                marginBottom: '20px',
                color: '#4a5568',
                lineHeight: '1.5'
              }}>
                     .
                   <strong>@{currentProfile?.login}</strong>:
              </p>
              
              <TextArea
                value={superlikeMessage}
                onChange={(e) => setSuperlikeMessage(e.target.value)}
                placeholder="!    ()..."
                $minHeight="120px"
                style={{ marginBottom: '20px' }}
                maxLength={500}
              />
              
              <div style={{
                display: 'flex',
                gap: '15px',
                justifyContent: 'flex-end'
              }}>
                <Button
                  $variant="secondary"
                  onClick={handleCloseSuperlikeModal}
                >
                  
                </Button>
                <Button
                  onClick={handleSendSuperlike}
                  disabled={!superlikeMessage.trim() || superlikeMutation.isLoading}
                >
                  {superlikeMutation.isLoading ? '...' : ' '}
                </Button>
              </div>
              
              <p style={{
                marginTop: '15px',
                fontSize: '12px',
                color: '#718096',
                textAlign: 'center'
              }}>
                   : 
              </p>
            </div>
          </ModalContent>
        </Modal>
      )}
    </HomeContainer>
  );
};

export default Home;