import React, { useState, useEffect } from 'react';
import { clubApi } from '../services/clubApi';
import toast from 'react-hot-toast';
import '../styles/ClubSettings.css';

// 
const SaveIcon = () => (
  <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
    <path d="M19 21H5a2 2 0 0 1-2-2V5a2 2 0 0 1 2-2h11l5 5v11a2 2 0 0 1-2 2z"/>
    <polyline points="17,21 17,13 7,13 7,21"/>
    <polyline points="7,3 7,8 15,8"/>
  </svg>
);

const UploadIcon = () => (
  <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
    <path d="M21 15v4a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2v-4"/>
    <polyline points="7,10 12,15 17,10"/>
    <line x1="12" y1="15" x2="12" y2="3"/>
  </svg>
);

const EyeIcon = () => (
  <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
    <path d="M1 12s4-8 11-8 11 8 11 8-4 8-11 8-11-8-11-8z"/>
    <circle cx="12" cy="12" r="3"/>
  </svg>
);

const EyeOffIcon = () => (
  <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
    <path d="M17.94 17.94A10.07 10.07 0 0 1 12 20c-7 0-11-8-11-8a18.45 18.45 0 0 1 5.06-5.94M9.9 4.24A9.12 9.12 0 0 1 12 4c7 0 11 8 11 8a18.5 18.5 0 0 1-2.16 3.19m-6.72-1.07a3 3 0 1 1-4.24-4.24"/>
    <line x1="1" y1="1" x2="23" y2="23"/>
  </svg>
);

const ClubSettings = () => {
  const [club, setClub] = useState(null);
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [activeTab, setActiveTab] = useState('general');
  const [showPasswordForm, setShowPasswordForm] = useState(false);

  useEffect(() => {
    loadClubData();
  }, []);

  const loadClubData = async () => {
    try {
      setLoading(true);
      const response = await clubApi.getProfile();
      setClub(response.club || response);
    } catch (error) {
      console.error('   :', error);
    } finally {
      setLoading(false);
    }
  };

  const handleSave = async (formData) => {
    try {
      setSaving(true);
      await clubApi.updateProfile(formData);
      await loadClubData();
      toast.success('  !');
    } catch (error) {
      console.error('  :', error);
      toast.error('   ');
    } finally {
      setSaving(false);
    }
  };

  const handleChangePassword = async (passwordData) => {
    try {
      setSaving(true);
      await clubApi.changePassword(passwordData);
      setShowPasswordForm(false);
      toast.success('  !');
    } catch (error) {
      console.error('  :', error);
      toast.error('   ');
    } finally {
      setSaving(false);
    }
  };

  const handleUploadAvatar = async (file) => {
    try {
      setSaving(true);
      const formData = new FormData();
      formData.append('avatar', file);
      await clubApi.uploadAvatar(formData);
      await loadClubData();
      toast.success('  !');
    } catch (error) {
      console.error('  :', error);
      toast.error('   ');
    } finally {
      setSaving(false);
    }
  };

  if (loading) {
    return (
      <div className="club-settings">
        <div className="loading">
          <div className="spinner"></div>
          <p> ...</p>
        </div>
      </div>
    );
  }

  const tabs = [
    { id: 'general', label: '', icon: '' },
    { id: 'profile', label: '', icon: '' },
    { id: 'privacy', label: '', icon: '' },
    { id: 'notifications', label: '', icon: '' },
    { id: 'security', label: '', icon: '' }
  ];

  return (
    <div className="club-settings">
      <div className="page-header">
        <div className="header-content">
          <h1> </h1>
          <p>     </p>
        </div>
      </div>

      <div className="settings-container">
        <div className="settings-sidebar">
          <div className="tabs-list">
            {tabs.map((tab) => (
              <button
                key={tab.id}
                className={`tab-button ${activeTab === tab.id ? 'active' : ''}`}
                onClick={() => setActiveTab(tab.id)}
              >
                <span className="tab-icon">{tab.icon}</span>
                <span className="tab-label">{tab.label}</span>
              </button>
            ))}
          </div>
        </div>

        <div className="settings-content">
          {activeTab === 'general' && (
            <GeneralSettings club={club} onSave={handleSave} saving={saving} />
          )}
          
          {activeTab === 'profile' && (
            <ProfileSettings 
              club={club} 
              onSave={handleSave} 
              onUploadAvatar={handleUploadAvatar}
              saving={saving} 
            />
          )}
          
          {activeTab === 'privacy' && (
            <PrivacySettings club={club} onSave={handleSave} saving={saving} />
          )}
          
          {activeTab === 'notifications' && (
            <NotificationSettings club={club} onSave={handleSave} saving={saving} />
          )}
          
          {activeTab === 'security' && (
            <SecuritySettings 
              club={club} 
              onSave={handleSave} 
              onChangePassword={handleChangePassword}
              showPasswordForm={showPasswordForm}
              setShowPasswordForm={setShowPasswordForm}
              saving={saving} 
            />
          )}
        </div>
      </div>
    </div>
  );
};

//   
const GeneralSettings = ({ club, onSave, saving }) => {
  const [formData, setFormData] = useState({
    name: club?.name || '',
    description: club?.description || '',
    type: club?.type || 'general',
    website: club?.website || '',
    contact_info: club?.contact_info || ''
  });

  const handleSubmit = (e) => {
    e.preventDefault();
    onSave(formData);
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  return (
    <div className="settings-section">
      <h2> </h2>
      <form onSubmit={handleSubmit} className="settings-form">
        <div className="form-group">
          <label htmlFor="name">  *</label>
          <input
            type="text"
            id="name"
            name="name"
            value={formData.name}
            onChange={handleChange}
            required
            placeholder="  "
          />
        </div>

        <div className="form-group">
          <label htmlFor="description"> </label>
          <textarea
            id="description"
            name="description"
            value={formData.description}
            onChange={handleChange}
            rows="4"
            placeholder="  "
          />
        </div>

        <div className="form-row">
          <div className="form-group">
            <label htmlFor="type"> </label>
            <select id="type" name="type" value={formData.type} onChange={handleChange}>
              <option value="general"></option>
              <option value="swing"></option>
              <option value="bdsm"></option>
              <option value="fetish"></option>
              <option value="lifestyle"></option>
              <option value="other"></option>
            </select>
          </div>
        </div>

        <div className="form-group">
          <label htmlFor="website">-</label>
          <input
            type="url"
            id="website"
            name="website"
            value={formData.website}
            onChange={handleChange}
            placeholder="https://example.com"
          />
        </div>

        <div className="form-group">
          <label htmlFor="contact_info"> </label>
          <textarea
            id="contact_info"
            name="contact_info"
            value={formData.contact_info}
            onChange={handleChange}
            rows="3"
            placeholder="  "
          />
        </div>

        <div className="form-actions">
          <button type="submit" className="btn btn-primary" disabled={saving}>
            <SaveIcon className="icon" />
            {saving ? '...' : ''}
          </button>
        </div>
      </form>
    </div>
  );
};

//   
const ProfileSettings = ({ club, onSave, onUploadAvatar, saving }) => {
  const [formData, setFormData] = useState({
    country: club?.country || '',
    city: club?.city || '',
    address: club?.address || '',
    email: club?.email || ''
  });

  const handleSubmit = (e) => {
    e.preventDefault();
    onSave(formData);
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleFileChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      onUploadAvatar(file);
    }
  };

  return (
    <div className="settings-section">
      <h2> </h2>
      
      <div className="avatar-section">
        <h3> </h3>
        <div className="avatar-upload">
          <div className="current-avatar" onClick={() => document.getElementById('avatar-upload').click()}>
            {club?.avatar ? (
              <img 
                src={`/uploads/${club.avatar}`} 
                alt=" " 
                onError={(e) => {
                  e.target.src = '/uploads/no_photo.jpg';
                }}
              />
            ) : (
              <div className="avatar-placeholder"></div>
            )}
            <div className="avatar-overlay">
              <span></span>
              <span> </span>
            </div>
          </div>
          <div className="upload-controls">
            <label htmlFor="avatar-upload" className="btn btn-secondary">
              <UploadIcon className="icon" />
               
            </label>
            <input
              type="file"
              id="avatar-upload"
              accept="image/*"
              onChange={handleFileChange}
              style={{ display: 'none' }}
            />
            <p className="upload-hint"> : 200x200px</p>
          </div>
        </div>
      </div>

      <form onSubmit={handleSubmit} className="settings-form">
        <div className="form-row">
          <div className="form-group">
            <label htmlFor="country"></label>
            <input
              type="text"
              id="country"
              name="country"
              value={formData.country}
              onChange={handleChange}
              placeholder=""
            />
          </div>
          <div className="form-group">
            <label htmlFor="city"></label>
            <input
              type="text"
              id="city"
              name="city"
              value={formData.city}
              onChange={handleChange}
              placeholder=""
            />
          </div>
        </div>

        <div className="form-group">
          <label htmlFor="address"></label>
          <input
            type="text"
            id="address"
            name="address"
            value={formData.address}
            onChange={handleChange}
            placeholder="  "
          />
        </div>

        <div className="form-group">
          <label htmlFor="email">Email</label>
          <input
            type="email"
            id="email"
            name="email"
            value={formData.email}
            onChange={handleChange}
            placeholder="club@example.com"
          />
        </div>

        <div className="form-actions">
          <button type="submit" className="btn btn-primary" disabled={saving}>
            <SaveIcon className="icon" />
            {saving ? '...' : ''}
          </button>
        </div>
      </form>
    </div>
  );
};

