//   
const SecuritySettings = ({ club, onSave, onChangePassword, showPasswordForm, setShowPasswordForm, saving }) => {
  const [passwordData, setPasswordData] = useState({
    current_password: '',
    new_password: '',
    confirm_password: ''
  });

  const handlePasswordSubmit = (e) => {
    e.preventDefault();
    if (passwordData.new_password !== passwordData.confirm_password) {
      toast.error('  ');
      return;
    }
    
    //    API
    const apiData = {
      currentPassword: passwordData.current_password,
      newPassword: passwordData.new_password
    };
    
    onChangePassword(apiData);
    setPasswordData({ current_password: '', new_password: '', confirm_password: '' });
  };

  const handlePasswordChange = (e) => {
    const { name, value } = e.target;
    setPasswordData(prev => ({ ...prev, [name]: value }));
  };

  return (
    <div className="settings-section">
      <h2> </h2>
      
      <div className="security-section">
        <h3> </h3>
        <p>     </p>
        
        {!showPasswordForm ? (
          <button 
            className="btn btn-secondary"
            onClick={() => setShowPasswordForm(true)}
          >
             
          </button>
        ) : (
          <form onSubmit={handlePasswordSubmit} className="password-form">
            <div className="form-group">
              <label htmlFor="current_password"> </label>
              <input
                type="password"
                id="current_password"
                name="current_password"
                value={passwordData.current_password}
                onChange={handlePasswordChange}
                required
                placeholder="  "
              />
            </div>

            <div className="form-group">
              <label htmlFor="new_password"> </label>
              <input
                type="password"
                id="new_password"
                name="new_password"
                value={passwordData.new_password}
                onChange={handlePasswordChange}
                required
                placeholder="  "
                minLength="6"
              />
            </div>

            <div className="form-group">
              <label htmlFor="confirm_password"> </label>
              <input
                type="password"
                id="confirm_password"
                name="confirm_password"
                value={passwordData.confirm_password}
                onChange={handlePasswordChange}
                required
                placeholder="  "
                minLength="6"
              />
            </div>

            <div className="form-actions">
              <button type="button" className="btn btn-secondary" onClick={() => setShowPasswordForm(false)}>
                
              </button>
              <button type="submit" className="btn btn-primary" disabled={saving}>
                {saving ? '...' : ' '}
              </button>
            </div>
          </form>
        )}
      </div>

      <div className="security-info">
        <h3>  </h3>
        <div className="security-stats">
          <div className="security-item">
            <span className="security-label"> :</span>
            <span className="security-value">
              {club?.last_login ? new Date(club.last_login).toLocaleString('ru-RU') : ''}
            </span>
          </div>
          <div className="security-item">
            <span className="security-label">Email :</span>
            <span className="security-value">
              {club?.email_verified ? '' : ''}
            </span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ClubSettings;

