import React, { useState, useEffect } from 'react';
import { clubApi } from '../services/clubApi';
import ClubAvatarCropper from './UI/ClubAvatarCropper';
import EventImageUploader from './UI/EventImageUploader';
import toast from 'react-hot-toast';
import './EventForm.css';

const EventForm = ({ event, onSave, onCancel, clubId }) => {
  const [formData, setFormData] = useState({
    title: '',
    description: '',
    date: '',
    time: '',
    location: '',
    price: '',
    max_participants: '',
    category: 'party',
    age_restriction: '18+',
    dress_code: '',
    special_requirements: '',
    duration_hours: 2
  });
  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState({});
  const [showAvatarCropper, setShowAvatarCropper] = useState(false);
  const [selectedImageFile, setSelectedImageFile] = useState(null);
  const [eventImages, setEventImages] = useState([]);

  useEffect(() => {
    if (event) {
      const eventDate = new Date(event.date);
      setFormData({
        title: event.title || '',
        description: event.description || '',
        date: eventDate.toISOString().split('T')[0],
        time: eventDate.toTimeString().slice(0, 5),
        location: event.location || '',
        price: event.price || '',
        max_participants: event.max_participants || '',
        category: event.category || 'party',
        age_restriction: event.age_restriction || '18+',
        dress_code: event.dress_code || '',
        special_requirements: event.special_requirements || '',
        avatar: event.avatar || '',
        duration_hours: event.duration_hours || 2
      });
      
      //   
      setEventImages(event.images || []);
    }
  }, [event]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
    
    //     
    if (errors[name]) {
      setErrors(prev => ({
        ...prev,
        [name]: ''
      }));
    }
  };

  const validateForm = () => {
    const newErrors = {};

    if (!formData.title.trim()) {
      newErrors.title = '  ';
    }

    if (!formData.date) {
      newErrors.date = '  ';
    } else {
      const eventDate = new Date(`${formData.date}T${formData.time}`);
      if (eventDate <= new Date()) {
        newErrors.date = '     ';
      }
    }

    if (!formData.time) {
      newErrors.time = '  ';
    }

    if (formData.price && isNaN(parseFloat(formData.price))) {
      newErrors.price = '   ';
    }

    if (formData.max_participants && (isNaN(parseInt(formData.max_participants)) || parseInt(formData.max_participants) < 1)) {
      newErrors.max_participants = '     ';
    }

    if (formData.duration_hours && (isNaN(parseInt(formData.duration_hours)) || parseInt(formData.duration_hours) < 1 || parseInt(formData.duration_hours) > 24)) {
      newErrors.duration_hours = '    1  24 ';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleAvatarClick = () => {
    const input = document.createElement('input');
    input.type = 'file';
    input.accept = 'image/*';
    input.onchange = (e) => {
      const file = e.target.files[0];
      if (file) {
        setSelectedImageFile(file);
        setShowAvatarCropper(true);
      }
    };
    input.click();
  };

  const handleAvatarCrop = async (cropData) => {
    if (!event?.id) return;
    
    try {
      const formData = new FormData();
      formData.append('avatar', cropData.file);
      
      if (cropData.cropParams) {
        formData.append('x', cropData.cropParams.x);
        formData.append('y', cropData.cropParams.y);
        formData.append('width', cropData.cropParams.width);
        formData.append('height', cropData.cropParams.height);
      }
      
      const response = await clubApi.uploadEventAvatar(event.id, formData);
      
      if (response.success) {
        //    
        setFormData(prev => ({
          ...prev,
          avatar: response.filename
        }));
      }
    } catch (error) {
      console.error('  :', error);
      toast.error('    ');
    }
  };

  const handleCloseAvatarCropper = () => {
    setShowAvatarCropper(false);
    setSelectedImageFile(null);
  };

  const handleImageUpload = async (eventId, formData) => {
    try {
      const response = await clubApi.uploadEventImages(eventId, formData);
      
      if (response.success) {
        setEventImages(prev => [...prev, ...response.files.map(f => f.filename)]);
      }
      
      return response;
    } catch (error) {
      console.error('  :', error);
      throw error;
    }
  };

  const handleImageRemove = async (eventId, filename) => {
    try {
      await clubApi.deleteEventImage(eventId, filename);
      setEventImages(prev => prev.filter(img => img !== filename));
    } catch (error) {
      console.error('  :', error);
      throw error;
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }

    setLoading(true);
    try {
      const eventData = {
        ...formData,
        club_id: clubId,
        date: new Date(`${formData.date}T${formData.time}`).toISOString(),
        price: formData.price ? parseFloat(formData.price) : 0,
        max_participants: formData.max_participants ? parseInt(formData.max_participants) : null,
        duration_hours: formData.duration_hours ? parseInt(formData.duration_hours) : 2
      };

      if (event) {
        //   
        await clubApi.updateEvent(event.id, eventData);
      } else {
        //   
        await clubApi.createEvent(eventData);
      }

      onSave();
    } catch (error) {
      console.error('  :', error);
      setErrors({ submit: '  ' });
    } finally {
      setLoading(false);
    }
  };

  const categories = [
    { value: 'party', label: '' },
    { value: 'concert', label: '' },
    { value: 'workshop', label: '-' },
    { value: 'networking', label: '' },
    { value: 'exhibition', label: '' },
    { value: 'sports', label: '' },
    { value: 'other', label: '' }
  ];

  const ageRestrictions = [
    { value: '16+', label: '16+' },
    { value: '18+', label: '18+' },
    { value: '21+', label: '21+' },
    { value: 'all', label: ' ' }
  ];

  return (
    <div className="event-form-overlay">
      <div className="event-form">
        <div className="event-form-header">
          <h2>{event ? ' ' : ' '}</h2>
          <button 
            type="button" 
            className="close-button"
            onClick={onCancel}
          >
            
          </button>
        </div>

        <form onSubmit={handleSubmit} className="event-form-content">
          {errors.submit && (
            <div className="error-message">
              {errors.submit}
            </div>
          )}

          {/*   */}
          {event && (
            <div className="form-section">
              <h3> </h3>
              <div 
                className="event-avatar-upload"
                onClick={handleAvatarClick}
                style={{
                  width: '200px',
                  height: '80px',
                  border: '2px dashed #e2e8f0',
                  borderRadius: '12px',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  cursor: 'pointer',
                  backgroundColor: formData.avatar ? 'transparent' : '#f8fafc',
                  backgroundImage: formData.avatar ? `url(/uploads/${formData.avatar})` : 'none',
                  backgroundSize: 'cover',
                  backgroundPosition: 'center',
                  backgroundRepeat: 'no-repeat',
                  position: 'relative'
                }}
              >
                {!formData.avatar && (
                  <div style={{ textAlign: 'center', color: '#718096' }}>
                    <div style={{ fontSize: '1.5rem', marginBottom: '5px' }}></div>
                    <div style={{ fontSize: '12px' }}> </div>
                  </div>
                )}
                {formData.avatar && (
                  <div style={{
                    position: 'absolute',
                    top: 0,
                    left: 0,
                    right: 0,
                    bottom: 0,
                    background: 'rgba(0,0,0,0.5)',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    opacity: 0,
                    transition: 'opacity 0.3s ease',
                    color: 'white',
                    fontSize: '12px'
                  }}
                  onMouseEnter={(e) => e.target.style.opacity = '1'}
                  onMouseLeave={(e) => e.target.style.opacity = '0'}
                  >
                    
                  </div>
                )}
              </div>
            </div>
          )}

          {/*   */}
          {event && (
            <div className="form-section">
              <h3> </h3>
              <EventImageUploader
                eventId={event.id}
                onUpload={handleImageUpload}
                onRemove={handleImageRemove}
                existingImages={eventImages}
                maxFiles={10}
              />
            </div>
          )}

          <div className="form-row">
            <div className="form-group">
              <label htmlFor="title">  *</label>
              <input
                type="text"
                id="title"
                name="title"
                value={formData.title}
                onChange={handleChange}
                className={errors.title ? 'error' : ''}
                placeholder="  "
              />
              {errors.title && <span className="field-error">{errors.title}</span>}
            </div>

            <div className="form-group">
              <label htmlFor="category"></label>
              <select
                id="category"
                name="category"
                value={formData.category}
                onChange={handleChange}
              >
                {categories.map(cat => (
                  <option key={cat.value} value={cat.value}>
                    {cat.label}
                  </option>
                ))}
              </select>
            </div>
          </div>

          <div className="form-group">
            <label htmlFor="description"></label>
            <textarea
              id="description"
              name="description"
              value={formData.description}
              onChange={handleChange}
              rows="4"
              placeholder=" ..."
            />
          </div>

          <div className="form-row">
            <div className="form-group">
              <label htmlFor="date">  *</label>
              <input
                type="date"
                id="date"
                name="date"
                value={formData.date}
                onChange={handleChange}
                className={errors.date ? 'error' : ''}
                min={new Date().toISOString().split('T')[0]}
              />
              {errors.date && <span className="field-error">{errors.date}</span>}
            </div>

            <div className="form-group">
              <label htmlFor="time">  *</label>
              <input
                type="time"
                id="time"
                name="time"
                value={formData.time}
                onChange={handleChange}
                className={errors.time ? 'error' : ''}
              />
              {errors.time && <span className="field-error">{errors.time}</span>}
            </div>
          </div>

          <div className="form-group">
            <label htmlFor="location"> </label>
            <input
              type="text"
              id="location"
              name="location"
              value={formData.location}
              onChange={handleChange}
              placeholder="   "
            />
          </div>

          <div className="form-row">
            <div className="form-group">
              <label htmlFor="price"> ()</label>
              <input
                type="number"
                id="price"
                name="price"
                value={formData.price}
                onChange={handleChange}
                className={errors.price ? 'error' : ''}
                placeholder="0"
                min="0"
                step="0.01"
              />
              {errors.price && <span className="field-error">{errors.price}</span>}
            </div>

            <div className="form-group">
              <label htmlFor="max_participants">. </label>
              <input
                type="number"
                id="max_participants"
                name="max_participants"
                value={formData.max_participants}
                onChange={handleChange}
                className={errors.max_participants ? 'error' : ''}
                placeholder=" "
                min="1"
              />
              {errors.max_participants && <span className="field-error">{errors.max_participants}</span>}
            </div>

            <div className="form-group">
              <label htmlFor="duration_hours"> ()</label>
              <input
                type="number"
                id="duration_hours"
                name="duration_hours"
                value={formData.duration_hours}
                onChange={handleChange}
                className={errors.duration_hours ? 'error' : ''}
                placeholder="2"
                min="1"
                max="24"
              />
              {errors.duration_hours && <span className="field-error">{errors.duration_hours}</span>}
            </div>
          </div>

          <div className="form-row">
            <div className="form-group">
              <label htmlFor="age_restriction"> </label>
              <select
                id="age_restriction"
                name="age_restriction"
                value={formData.age_restriction}
                onChange={handleChange}
              >
                {ageRestrictions.map(age => (
                  <option key={age.value} value={age.value}>
                    {age.label}
                  </option>
                ))}
              </select>
            </div>

            <div className="form-group">
              <label htmlFor="dress_code">-</label>
              <input
                type="text"
                id="dress_code"
                name="dress_code"
                value={formData.dress_code}
                onChange={handleChange}
                placeholder=": , casual"
              />
            </div>
          </div>

          <div className="form-group">
            <label htmlFor="special_requirements"> </label>
            <textarea
              id="special_requirements"
              name="special_requirements"
              value={formData.special_requirements}
              onChange={handleChange}
              rows="3"
              placeholder="   ..."
            />
          </div>

          <div className="form-actions">
            <button 
              type="button" 
              className="btn btn-secondary"
              onClick={onCancel}
              disabled={loading}
            >
              
            </button>
            <button 
              type="submit" 
              className="btn btn-primary"
              disabled={loading}
            >
              {loading ? '...' : (event ? ' ' : ' ')}
            </button>
          </div>
        </form>

        {/*     */}
        <ClubAvatarCropper
          isOpen={showAvatarCropper}
          onClose={handleCloseAvatarCropper}
          imageFile={selectedImageFile}
          onCrop={handleAvatarCrop}
          aspectRatio={590/160}
        />
      </div>
    </div>
  );
};

export default EventForm;
